.animal {
  transition: 0.15s ease-in-out;
}

.animal:hover {
  transform: rotate(15deg) scale(1.7);
}

#pigZone > h1 {
  padding-top: 10px;
  z-index: 50;
}

.animals {
  display: flex;
  flex-direction: row;
  /*position: fixed;*/
  margin-top: 240px;
  margin-bottom: auto;
  z-index: 20;
  width: 90%;
  justify-content: space-around;
}

.animal {
  width: 25%;
  align-items: baseline;
}
